import { TTableContent } from "../renderers/RenderLayoutTable";
import { TEMPLATE_CLOSE, TEMPLATE_DELIMETER, TEMPLATE_OPEN } from "./constants";

export const generateOutput = (rawHtmlString: string, dataMapping: { [key: string]: string }) => {
    let output = [];
    if (rawHtmlString.includes(TEMPLATE_OPEN)) {
        const [currentContent, ...rest] = rawHtmlString.split(TEMPLATE_OPEN);
        output.push({
            input: false,
            inline: true,
            content: currentContent.split(/<p>/).join('<div>').split(/<\/p>/).join('</div>'),
            id: output.length,
        });
        rest.forEach((openSplit) => {
            if (openSplit.includes(TEMPLATE_CLOSE)) {
                const [contentIdentifier, ...closeSplit] = openSplit.split(TEMPLATE_CLOSE);
                if (contentIdentifier.startsWith('data')) {
                    output.push({
                        input: false,
                        content: dataMapping[contentIdentifier.split(TEMPLATE_DELIMETER)[1]],
                        inline: true,
                        id: output.length,
                    });
                } else {
                    output.push({
                        id: output.length,
                        input: true,
                        content: contentIdentifier,
                    });
                }
                output.push({
                    id: output.length,
                    input: false,
                    // inline: true,
                    content: closeSplit.join().split(/<p>/).join('<div>').split(/<\/p>/).join('</div>'),
                });
            } else {
                output.push({
                    id: output.length,
                    input: false,
                    content: openSplit.replace(/<p>/, '<div>'),
                });
            }
        });
    } else {
        output = [
            {
                id: output.length,
                input: false,
                content: rawHtmlString.replace(/<p>/, '<div>'),
            },
        ];
    }
    return output;
};

export const propsParser = (rawProps: string) => {
    let returnProps: any = {};
    if (rawProps) {
        const props = rawProps.split(' ');
        props.forEach((prop) => {
            const [key, value] = prop.split('=');
            if (value) {
                if (key === 'style') {
                    const [styles] = value.split(',');
                    let styleProp: any = {};
                    if (styles) {
                        const [property, val] = styles.split(':');
                        styleProp[property] = val;
                    }
                    returnProps[key] = { styleProp };
                } else {
                    returnProps[key] = value.split('"').join('');
                }
            }
        });
    }
    return returnProps;
};

const getProps = (raw: string) => {
    const [props] = raw.split('>')
    if (props) return propsParser(props)
    return {}
}

const getPropsAndContent = (raw: string, suffix: string) => {

    try {
        const [props, ...content] = raw.split('>')
        const [actualContent] = content.join('>').split(suffix)
        return { props: propsParser(props), content: actualContent }
    } catch {
        return { props: {}, content: '' }
    }
}

export const parseTable = (rawString: string, dataMapping: any) => {
    let parsed = [];
    // console.log('woah', rawString);

    if (rawString.includes('<table')) {
        const [beforeTable, actualTable] = rawString.split('<table');
        parsed.push(generateOutput(beforeTable, dataMapping));

        const [tableProps, ...rest] = actualTable.split('>');

        // console.log('tableProps', propsParser(tableProps));
        const [tableBody, afterTable] = rest.join('>').split('</table>');
        //@ts-ignore
        let tbody: TTableContent = { tableProps, header: [], body: [] }
        if (tableBody.includes('<thead')) {
            const [, afterHeaderTag] = tableBody.split('<thead');
            //@ts-ignore
            tbody.header[0] = { headerProps: getProps(afterHeaderTag), headerRows: [] }

            const [tableHeader] = afterHeaderTag.split('</thead>');
            const [_, ...tableHeaderRows] = tableHeader.split('<tr');
            if (tableHeaderRows) {
                tableHeaderRows.forEach((row, rowIndex) => {
                    //@ts-ignore
                    tbody.header[0].headerRows[rowIndex] = { headerCells: [], headerRowProps: getProps(row) }
                    const [_, ...tableHeaderCell] = row.split('<th');
                    if (tableHeaderCell) {
                        tableHeaderCell.forEach((data, cellIndex) => {
                            const { props, content } = getPropsAndContent(data, '</th>');

                            tbody.header[0].headerRows[rowIndex].headerCells[cellIndex] = { headerCell: '', headerCellProps: props }
                            tbody.header[0].headerRows[rowIndex].headerCells[cellIndex].headerCell = content.split('&nbsp;').join(' ');
                            // console.log('actual data ', actualData.split('>').join('').split('&nbsp;').join(' '));
                        });
                    }
                });
            }
        }
        if (tableBody.includes('<tbody')) {
            const [, afterBodyTag] = tableBody.split('<tbody');
            //@ts-ignore
            tbody.body[0] = { bodyProps: getProps(afterBodyTag), rows: [] }

            const [tableBodyContent] = afterBodyTag.split('</tbody>');
            const [_, ...tableRows] = tableBodyContent.split('<tr');
            if (tableRows) {
                tableRows.forEach((row, rowIndex) => {
                    // tbody.rows[rowIndex] = [];
                    //@ts-ignore
                    tbody.body[0].rows[rowIndex] = { rowProps: {}, cells: [] }
                    const [_, ...tableData] = row.split('<td');
                    if (tableData) {
                        tableData.forEach((data, cellIndex) => {
                            tbody.body[0].rows[rowIndex].cells[cellIndex] = { cell: '', cellProps: {} }
                            const [actualData] = data.split('</td>');
                            tbody.body[0].rows[rowIndex].cells[cellIndex].cell = actualData.split('>').join('').split('&nbsp;').join(' ');
                            // console.log('actual data ', actualData.split('>').join('').split('&nbsp;').join(' '));
                        });
                    }
                });
            }
        }

        // console.log(tbody, 'tabod');
        // 
        // console.log(`berfore after table`, tableBody);

        parsed.push({ input: false, content: JSON.stringify(tbody), id: 988, table: true });
        parsed.push(generateOutput(afterTable, dataMapping));
        console.log(tbody);
    }
    return parsed.flat();
};